<style lang="scss">
</style>


<template>
	<div class="components_tabs__content" v-show="getSelectedTab() == name">
		<slot></slot>
	</div>
</template>


<script lang="js">

import { defineComponent } from 'vue';
import { mixin } from 'vue-helper';

export const Tab =
{
	mixins: [ mixin ],
	props: {
        name: { required: true },
        selected: { default: false}
    },
	methods:{
		getSelectedTab: function(){
			return this.$parent.selected_tab;
		},
	},
}

export default defineComponent(Tab);

</script>